.day-sign {
  height: 100%;
  background: #3C53FE;
}
.day-sign .ant-spin-nested-loading,
.day-sign .ant-spin-container {
  height: 100%;
}
.day-sign .day-header {
  background: #3C53FE;
  box-shadow: 0px 11px 35px 0px rgba(47, 14, 77, 0.46);
  width: 100%;
  height: 25%;
  padding: 0.32rem;
  padding-bottom: 3%;
  color: #fff;
  line-height: 0.32rem;
}
.day-sign .day-header .header {
  padding-top: 0.36rem;
  margin-bottom: 0.45rem;
}
.day-sign .day-header .header h3 {
  color: #fff;
  text-align: center;
  font-size: 0.32rem;
  font-weight: 500;
}
.day-sign .day-header .header .icon {
  font-size: 0.32rem;
}
.day-sign .day-header .check-day {
  width: 100%;
  height: 1.2rem;
}
.day-sign .day-header .check-day .header-box {
  position: relative;
  text-align: center;
}
.day-sign .day-header .check-day .header-box img {
  margin-bottom: 0.2rem;
  width: 50%;
  height: 50%;
}
.day-sign .day-header .check-day .header-box .gift-img {
  width: 55%;
}
.day-sign .day-header .check-day .hearder-day {
  font-size: 0.2rem;
}
.day-sign .day-center {
  width: 100%;
  height: 75%;
  background: linear-gradient(0deg, #465cff 0%, #011efa 100%);
  padding-bottom: 5%;
}
.day-sign .day-center .center-header {
  width: 6.86rem;
  height: 2.1rem;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
  margin: 0 auto;
  border-bottom-left-radius: 0.12rem;
  border-bottom-right-radius: 0.12rem;
  padding-top: 0.15rem;
}
.day-sign .day-center .center-header .center {
  margin: 0 auto;
  width: 93%;
  height: 1.5rem;
}
.day-sign .day-center .center-header .center .check {
  width: 2.7rem;
  height: 0.51rem;
  font-size: 0.32rem;
  font-weight: 500;
  color: #393441;
  line-height: 0.52rem;
  margin-top: 0.3rem;
  padding-left: 0.1rem;
}
.day-sign .day-center .center-header .center .header-img {
  width: 100%;
  height: 100%;
}
.day-sign .day-center .center-header .center .check-red {
  color: #3C53FE;
  padding: 0 0.06rem;
}
.day-sign .day-center .center-header .center .obtain {
  width: 2.8rem;
  height: 0.28rem;
  font-size: 0.24rem;
  font-weight: 400;
  color: #9f9fac;
  line-height: 0.28rem;
  padding-left: 0.1rem;
  margin-top: 0.14rem;
}
.day-sign .day-center .center-header .center .obtain-red {
  color: #3C53FE;
}
.day-sign .day-center .center-header .center .check-button {
  width: 1.8rem;
  height: 0.7rem;
  background: #fedc55;
  box-shadow: 0px 5px 27px 0px rgba(130, 130, 130, 0.17);
  border-radius: 35px;
  font-size: 0.26rem;
  font-weight: 500;
  margin-top: 0.4rem;
}
.day-sign .day-center .center-center {
  width: 6.86rem;
  height: 1.4rem;
  margin: 0 auto;
  line-height: 1.4rem;
  text-align: center;
  color: #fff;
  font-weight: 400;
  font-size: 0.36rem;
}
.day-sign .day-center .center-center .task {
  width: 2.6rem;
}
.day-sign .day-center .center-center .task-left,
.day-sign .day-center .center-center .task-right {
  width: 0.2rem;
}
.day-sign .day-center .center-center .task-right {
  margin-left: 0.1rem;
}
.day-sign .day-center .center-center .task-left {
  margin-right: 0.1rem;
  transform: rotate(180deg);
}
.day-sign .day-center .center-foot {
  width: 6.86rem;
  height: 62%;
  margin: 0 auto;
  background: #fff;
  border-radius: 12px;
  padding: 0.4rem;
}
.day-sign .day-center .center-foot .center-img {
  width: 100%;
  height: 2.5rem;
  margin-bottom: 0.3rem;
}
.day-sign .day-center .center-foot .center-img img {
  width: 100%;
}
.day-sign .day-center .center-foot .center-top,
.day-sign .day-center .center-foot .center-bottom {
  width: 100%;
  height: 1.4rem;
}
.day-sign .day-center .center-foot .center-top {
  border-bottom: 1px dashed #dcdcdc;
}
.day-sign .day-center .center-foot .center-bottom {
  margin-top: 0.25rem;
}
.day-sign .day-center .center-foot .top-img {
  margin-top: 0.2rem;
  margin-left: 0.2rem;
  width: 60%;
}
.day-sign .day-center .center-foot .inv {
  width: 2.4rem;
  height: 0.51rem;
  font-size: 0.28rem;
  font-weight: 500;
  color: #393441;
  line-height: 0.51rem;
  margin-top: 0.15rem;
}
.day-sign .day-center .center-foot .inv-span {
  width: 2.8rem;
  height: 0.28rem;
  font-size: 0.24rem;
  font-weight: 400;
  color: #9f9fac;
  line-height: 0.28rem;
  margin-top: 0.06rem;
}
.day-sign .day-center .center-foot .center-button {
  width: 1.6rem;
  height: 0.55rem;
  line-height: 0.55rem;
  border-radius: 35px;
  font-size: 0.3rem;
  font-weight: 400;
  margin-top: 0.35rem;
  margin-left: 0.2rem;
  color: #3F1D8A;
  border: 1px solid #3F1D8A;
}
.day-sign .day-center .check-modal {
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
  width: 100%;
  height: 100%;
  opacity: 0.67;
  z-index: 2;
}
.day-sign .day-center .modal-center {
  position: absolute;
  background-image: url(https://shequ-test.dxapp.net/static/media/backcheckin.4837131f.png);
  background-size: 100% 100%;
  top: 30%;
  left: 0.5rem;
  width: 6.37rem;
  height: 5.67rem;
  z-index: 10;
  color: #fff;
}
.day-sign .day-center .modal-center .checkin {
  font-size: 0.4rem;
  text-align: center;
  padding-top: 10%;
}
.day-sign .day-center .modal-center .checkin span {
  padding: 0 0.2rem;
}
.day-sign .day-center .modal-center p {
  text-align: center;
  padding-top: 24%;
  font-size: 0.2rem;
}
.day-sign .day-center .modal-center .modal-button {
  width: 3.6rem;
  height: 0.8rem;
  box-shadow: 0px 7px 18px 0px rgba(106, 49, 144, 0.36);
  background: #3C53FE;
  border-radius: 36px;
  text-align: center;
  line-height: 0.8rem;
  margin-top: 19%;
  margin-left: 1.4rem;
  font-size: 0.28rem;
}
